html,body,h1,h2,h3,h4,ul,ol,dl,li,dt,dd,p,div,span,img,a,table,tr,th,td{margin:0;padding:0;border:0;font-weight:normal;font-size:100%;vertical-align:baseline;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}article,header,footer,aside,figure,figcaption,nav,section{display:block}body{line-height:1;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}ol,ul{list-style:none;list-style-type:none}:root{font-size:62.5%}html{background:#f0f0f0}body{text-align:center;font:1.6rem/1.7 "游ゴシック","Yu Gothic",YuGothic,"ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic ProN","ヒラギノ角ゴ ProN",Meiryo,"メイリオ",Helvetica,Arial,Sans-Serif;font-weight:500;font-feature-settings:"pwid";background:#f0f0f0}*:hover{transition:.3s ease}#clock{position:fixed;bottom:20px;right:0;left:0;color:#ccc;line-height:1.4}#clock .time{letter-spacing:.1em;font-size:26px}#clock .time span{font-weight:bold}#clock .date{font-size:17px}@media screen and (max-width: 1250px){:root{font-size:45%}.wrap{width:90%}}@media screen and (max-width: 768px){:root{font-size:30%}.wrap{width:95%}}
